body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.header{
    display: flex;
    justify-content: space-evenly;
    margin-bottom:1rem;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 5rem;
    background: lightcoral;
}
.title{
   
    color: white;
    text-shadow: 2px 2px 4px #000000;
}
.scores{
    margin-top: 1rem;
}


.container{
display: flex;
flex-wrap: wrap;

align-content: flex-start;
justify-content: center;
width:100%;
}
.card {
    background: #fff;
    border-radius: 2px;
    height: 215px;
    margin: 1rem;
    position: relative;
    width: 300px;
    box-shadow: 0 4px 7px lightblue, 0 4px 7px lightblue;
    
  }

  .card:hover{
    box-shadow: 0 4px 7px lightcoral, 0 4px 7px lightcoral;
    
  }



img {
    height: 200px;
    width: 300px;
}
.text-center{
  text-align: center;
}

.text-right{
  text-align: right;
}

.text-left{
  text-align: left;
}

